.Grid {
  border: 1vw solid rgba(0, 0, 0, 0.4);
  border-radius: 3px; }
  .Grid-table {
    border-collapse: collapse; }
  .Grid-caption {
    caption-side: bottom;
    padding-top: 1vw;
    font-size: 2.5vh;
    background-color: rgba(0, 0, 0, 0.4); }
  .Grid-body {
    background-color: rgba(0, 0, 0, 0.2); }
    .Grid-body:hover {
      cursor: pointer; }



